<template>
  <el-container style="height: 100%">


    <el-header>
      <div class="head_main">
        <el-tabs v-model="activeName" @tab-click="handleClick" class="tabs">

          <el-tab-pane v-for="(data,index) in tags_router" :key="index" v-bind:label="data.name"
                       v-bind:name="data.url"/>

        </el-tabs>

        <div class="top_center">
          <img src="https://pic.superbed.cn/item/5da50d93451253d178976543.png"
               width="200px" height="100%" v-on:click="go2Home"/>

        </div>

        <div class="top_right_main">
          <!--          <div>-->
          <!--            <el-input v-model="input" placeholder="搜索" style="width: 200px"/>-->
          <!--            <i class="el-icon-search" @click="action_search"></i>-->
          <!--          </div>-->

          <el-tabs v-model="activeName" @tab-click="handleClick" class="tabs" style="margin-left: 10px">
            <el-tab-pane label="CONTACT US" name="five"/>

            <!--<el-tab-pane label="第六个" name="six"/>-->


          </el-tabs>

          <!--<router-link :to="{ name: 'manager'}" target="_blank">管理</router-link>-->

        </div>
      </div>


    </el-header>
    <el-main>


      <keep-alive>
        <router-view></router-view>
      </keep-alive>


    </el-main>


  </el-container>

</template>


<script>

  import Tab1 from "./Tab1";
  import Tab2 from "./Tab2";
  import Tab0 from "./Tab0";
  import BaseUtils from '../utils/BaseUtils'

  import $ from 'jquery'

  export default {
    name: "Home",
    components: {Tab0, Tab2, Tab1},
    data() {
      return {
        activeName: 'type0',
        input: '',
        tags_router: this.Common_Base.tags_router

      };
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab.paneName);
        this.activeName = tab.name;
        this.$router.push(this.activeName)
      },
      go2Home() {
        console.log("----gohome")
        // this.activeName = 'first';
        // this.$router.push(this.activeName)
      },
      action_search() {
        console.log(this.input)
      }
    },
    comments: {
      Tab1
    },
    // computed: {
    //     key() {
    //         return this.$route.path + Math.random()
    //     }
    // },
    mounted() {

      console.log(this.tags_router)
      // if(this.$route.params.userId){
      //   this.activeName='seven'
      // }

      this.$router.push(this.activeName)

      console.log(BaseUtils)
      BaseUtils.haha('hahahahaha')

      //count++
      const query = Bmob.Query(this.Common_Base.SQL_Pahsa_Base);
      var newDate = new Date()
      var result = newDate.Format("yyyy-MM-dd")
      console.log(result)

      query.equalTo("date", "==", result);
      query.find().then(res => {
        if (res.length == 0) {
          query.set("date", result)
          query.set("count", 0)
        } else {

          let count = 0;
          if (res[0]['count'] != undefined) {
            count = res[0]['count'] + 1
          }
          console.log(count)
          query.set("date", result)
          query.set("count", count)
          query.set("id", res[0]['objectId'])
        }

        query.save().then(res => {
          console.log(res)
        }).catch(err => {
          console.log(err)
        })
        //存在 count++
      }, error => {

      })


      $.getJSON('http://www.geoplugin.net/json.gp?jsoncallback=?', function(data) {
         let result1=JSON.stringify(data, null, 2);
         console.log(result1)
         let ip=data.geoplugin_request
         let geoplugin_city=data.geoplugin_city
         let location=data.geoplugin_latitude+','+data.geoplugin_longitude

        query.equalTo("date", "==", result);
        query.find().then(res => {
          if (res.length == 0) {
            query.set("date", result)
            query.set("count", 0)
            query.set("ip", ip)
            query.set("location_name", geoplugin_city)
            query.set("location", location)
          } else {
            query.set("id", res[0]['objectId'])
            query.set("date", result)
            query.set("ip", ip)
            query.set("location_name", geoplugin_city)
            query.set("location", location)
          }

          query.save().then(res => {
            console.log(res)
          }).catch(err => {
            console.log(err)
          })
          //存在 count++
        }, error => {

        })

      });

    }

  }

</script>

<style lang="stylus" scoped>

  @import '~@/style/skewd_bg.styl'

  .head_main
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;

    .tabs
      display: flex;
      width: 60%;

      padding-left: 24px;
      padding-right: 24px;
      justify-content: center;

    .top_center

      display flex
      flex 1
      justify-content center

    .top_right_main
      display: flex;
      width: 20%;
      align-items: center;
      justify-content: center


  .el-header {
    line-height: 60px;

    display: flex;
    width 100%

  }

  .el-container {
    display flex;
    flex-direction column;
    align-items center;

  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    width 75%
  }

  .el-dropdown-link {
    cursor: pointer;
    color: #409EFF;

  }

  div img {
    cursor: pointer;
    transition: all 0.6s;
  }

  div img:hover {

    -webkit-transform: rotate(360deg) scale(1.6); /*兼容Chrome*/

    -moz-transform: rotate(360deg) scale(1.6); /*兼容Firefox*/

    -ms-transform: rotate(360deg) scale(1.6); /*兼容IE*/

    -o-transform: rotate(360deg) scale(1.6); /*兼容Opera*/
  }


</style>



